.app-container {
  padding: 1rem;
  font-family: Arial, sans-serif;
  background-color: #f7f7f7;
}

.app-header {
  text-align: center;
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.msr-title {
  font-weight: bold;
  color: #007bff;
}

.search-bar {
  display: block;
  margin: 0 auto 1rem auto;
  padding: 0.5rem;
  font-size: 1rem;
  width: 90%;
  max-width: 400px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.table-container {
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}

.leaderboard-table {
  width: 100%;
  min-width: 900px;
  border-collapse: collapse;
}

.leaderboard-table th,
.leaderboard-table td {
  padding: 0.6rem;
  border: 1px solid #ccc;
  text-align: center;
  white-space: nowrap;
}

.leaderboard-table th {
  background-color: #0056b3;
  color: #fff;
  cursor: pointer;
  position: sticky;
  top: 0;
  z-index: 1;
}

.highlight-header {
  background-color: #007bff !important;
}

.highlight-msr {
  background-color: #007bff;
  color: white;
  font-weight: bold;
}

@media screen and (max-width: 768px) {
  .leaderboard-table {
    font-size: 0.85rem;
  }

  .search-bar {
    width: 100%;
  }
}















